import { useState } from "react";
import { useTranslation } from "react-i18next";
import { format } from "date-fns";
import { Coins, MessageSquare, Gift, ShoppingBag, ArrowDown, ArrowUp, RefreshCw, Clock, History } from "lucide-react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Badge } from "@/components/ui/badge";
import { usePointsTransactions, PointsTransaction } from "@/hooks/usePoints";

export const PointsHistoryDialog = () => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const { data: transactions = [], isLoading } = usePointsTransactions();
  const [filter, setFilter] = useState<'all' | 'earned' | 'spent'>('all');

  const getTypeIcon = (type: PointsTransaction['type']) => {
    switch (type) {
      case 'comment':
        return <MessageSquare className="w-4 h-4" />;
      case 'admin_grant':
        return <Gift className="w-4 h-4" />;
      case 'admin_deduct':
        return <ArrowDown className="w-4 h-4" />;
      case 'purchase':
        return <ShoppingBag className="w-4 h-4" />;
      case 'refund':
        return <RefreshCw className="w-4 h-4" />;
      case 'expire':
        return <Clock className="w-4 h-4" />;
      default:
        return <Coins className="w-4 h-4" />;
    }
  };

  const getTypeLabel = (type: PointsTransaction['type']) => {
    return t(`points.type.${type}`);
  };

  const filteredTransactions = transactions.filter((tx) => {
    if (filter === 'all') return true;
    if (filter === 'earned') return tx.amount > 0;
    if (filter === 'spent') return tx.amount < 0;
    return true;
  });

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button variant="ghost" size="sm" className="h-6 px-2">
          <History className="h-3 w-3 mr-1" />
          {t("profile.pointsHistory", "明细")}
        </Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-lg max-h-[80vh]">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <Coins className="w-5 h-5 text-accent" />
            {t('points.history')}
          </DialogTitle>
          <DialogDescription>
            {t('points.historyDesc')}
          </DialogDescription>
        </DialogHeader>

        <Tabs value={filter} onValueChange={(v) => setFilter(v as any)}>
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="all">{t('points.filter.all')}</TabsTrigger>
            <TabsTrigger value="earned">{t('points.filter.earned')}</TabsTrigger>
            <TabsTrigger value="spent">{t('points.filter.spent')}</TabsTrigger>
          </TabsList>

          <TabsContent value={filter} className="mt-4">
            <ScrollArea className="h-[400px] pr-4">
              {isLoading ? (
                <div className="text-center py-8 text-muted-foreground">
                  {t('common.loading')}
                </div>
              ) : filteredTransactions.length === 0 ? (
                <div className="text-center py-8 text-muted-foreground">
                  {t('points.noTransactions')}
                </div>
              ) : (
                <div className="space-y-3">
                  {filteredTransactions.map((tx) => (
                    <div
                      key={tx.id}
                      className="flex items-start justify-between p-3 rounded-lg border bg-card"
                    >
                      <div className="flex items-start gap-3">
                        <div className={`p-2 rounded-full ${
                          tx.amount > 0 
                            ? 'bg-green-500/10 text-green-500' 
                            : 'bg-red-500/10 text-red-500'
                        }`}>
                          {getTypeIcon(tx.type)}
                        </div>
                        <div>
                          <div className="font-medium flex items-center gap-2">
                            {getTypeLabel(tx.type)}
                            {tx.reason && (
                              <Badge variant="outline" className="text-xs font-normal">
                                {tx.reason}
                              </Badge>
                            )}
                          </div>
                          <div className="text-sm text-muted-foreground">
                            {format(new Date(tx.created_at), 'yyyy-MM-dd HH:mm')}
                          </div>
                          <div className="text-xs text-muted-foreground mt-1">
                            {t('points.balanceAfter')}: {tx.balance_after}
                          </div>
                        </div>
                      </div>
                      <div className={`font-bold flex items-center gap-1 ${
                        tx.amount > 0 ? 'text-green-500' : 'text-red-500'
                      }`}>
                        {tx.amount > 0 ? (
                          <ArrowUp className="w-4 h-4" />
                        ) : (
                          <ArrowDown className="w-4 h-4" />
                        )}
                        <span>{tx.amount > 0 ? '+' : ''}{tx.amount}</span>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </ScrollArea>
          </TabsContent>
        </Tabs>
      </DialogContent>
    </Dialog>
  );
};
